section {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: 80vw;
  max-height: 70vh;
  margin: auto auto;
  // margin: 100px 1.5rem 7.5rem 1.5rem;
  padding: 15px;
  border: black dotted 2px;
}
.envelope {
  background-image: url("..//img/koperta.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  height: 100vh;
  color: white;
}
input,
textarea {
  background-color: rgba(199, 199, 191, 0.867) !important;
  color: black !important;
}
.envelope .btn {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  color: white !important;
}
@media (max-width: 700px) {
  section {
    font-size: 15px;
    // max-width: 95vw;
    max-height: 75vh;
  }
  .envelope {
    margin: 60px 0 0 0;
    position: relative;
  }
  .envelope h2 {
    position: relative;
    // margin: 0% !important;
    font-size: 1.5rem;
    margin-top: 5px !important;
  }
  .envelope .line {
    // height: 5px;
    width: 3px;
  }
}
